@import './_variables'
@import './_extensional'
.container--footer
    @include container(100%, 100vh, center, column)
    .footer--wrapper
        @include container(900px, auto, space-between, row)
        margin-bottom: 50px
        div
            width: 46%
        .footer--info
            @include container(100%, auto, center, column)
            h2
                @include textFormation($fontText, $blackText, 900, 35px)
                margin-bottom: 10px
            p
                @include textFormation($fontText, $greyColor, 500, 15px)
                margin-bottom: 25px
            .footer--btms--conteiner
                @include container(100%, auto, space-between, row)
                .footer--btm--wrap
                    @include container(170px, auto, start, row)
                    background-color: black
                    border-radius: 8px
                    padding: 8px 15px
                    cursor: pointer
                    img
                        width: 22px
                    .footer--btm--text
                        p:nth-child(1)
                            @include textFormation($fontText, $whiteColor, 300, 10px)
                            margin-bottom: 5px
                        p:nth-child(2)
                            @include textFormation($fontText, $whiteColor, 500, 14px)
                            margin: 0
        .footer--img
            width: 46%
            img
                width: 100%
    .footer--deatals
        @include container(900px, auto, space-between, row)
        align-items: start

        .footer--deatal--item
            @include container(180px, auto, start, column)
            align-items: start

            h3
                @include textFormation($fontText, $blackText, 900, 20px)
                margin-bottom: 10px
                span
                    font-weight: 500
            p
                @include textFormation($fontText, $greyColor, 300, 15px)
                margin-bottom: 10px

            .footer-contact-wrap
                @include container(100%, auto, start, row)
                margin-bottom: 10px
                img
                    width: 20px
                    margin-right: 8px
                a
                    text-decoration: none
                    color: $btmBrandColor
                    font-size: 17px
                    cursor: pointer

            .company--link
                @include textFormation($fontText, $greyColor, 500, 15px)
                cursor: pointer
                text-decoration: none
                margin-bottom: 5px
            .work--time
                font-weight: 500
            .sub-by-email
                width: 100%
                input
                    width: 100%
                    border: none
                    margin-bottom: 10px
                    padding: 10px 15px
                    @include textFormation($fontText, $greyColor, 500, 12px)
                    border-bottom: 2px solid $greyColor
                button
                    @include btn($btmBrandColor, $whiteColor, $fontText, 5px, 10px 20px, 20px, none, 100%)
                    font-weight: 900
